#beginButton,
#restartButton {
  font-size: 1.15vw;
  color: white;
  background-color: #494b4d;
  border: 1px solid #494b4d;
  border-radius: 50%;
  height: 115px;
  width: 115px;
}

#beginButton:hover,
#restartButton:hover {
  cursor: pointer;
  background-color: #1f2225;
}

#formButtons {
  margin-top: 30px;
}

#playerButtons {
  margin-top: 5px;
  height: 40px;
}

#playerButtons .row,
#playerButtons .col {
  height: 40px;
}

.formBtn {
  font-weight: 100;
  font-size: 0.8vw;
  padding: 0.75vh 1vw;
  color: white;
  background-color: #494b4d;
  border: 1px solid #494b4d;
  border-radius: 5px;
  line-height: 0.8vw;
}

.formBtn:hover {
  cursor: pointer;
  background-color: #1f2225;
  border-color: #1f2225;
}

.formBtn:disabled {
  pointer-events: none;
  color: #494b4d;
  background-color: #898c8d;
  background: none;
  border: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.playerBtn {
  font-family: "Cormorant";
  font-size: 2vw;
  height: 40px;
  color: #494b4d;
  background-color: transparent;
  background: none;
  border: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.playerBtn:hover {
  cursor: pointer;
  color: #1f2225;
}

.playerBtn:focus,
.playerBtnDisabled:focus {
  outline: none;
}

.playerBtnDisabled {
  font-family: "Cormorant";
  font-size: 2vw;
  height: 40px;
  pointer-events: none;
  color: #898c8d;
  background-color: transparent;
  background: none;
  border: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.playerBtnText {
  font-family: "Baloo Thambi 2";
  font-size: 1.25vw;
  vertical-align: middle;
}
.playerBtnIcon {
  height: 40px;
  line-height: 40px;
  margin: 0;
  padding: 0;
}
