#currentPlayer {
  text-align: center;
  font-size: 2rem;
  margin-bottom: 5%;
}
#stepIconRow {
  height: 15px;
  margin-top: 20px;
  display: inline-block;
}

#formArea {
  margin: auto;
}

.stepIcons {
  height: 15px;
}
.stepIconActive {
  height: 15px;
  width: 15px;
  margin: 0 2px;
  background-color: #8c9299;
  border: none;
  border-radius: 50%;
  display: inline-block;
  opacity: 0.5;
}

.stepIcon {
  height: 15px;
  width: 15px;
  margin: 0 2px;
  background-color: #eeeeee;
  border: none;
  border-radius: 50%;
  display: inline-block;
  opacity: 0.5;
}
