@import "./components/styles/FormArea.css";
@import "./components/styles/buttons.css";
@import "./components/styles/Header.css";
@import "./components/styles/forms.css";
@import "./components/styles/Panel.css";
@import "./components/styles/canvas.css";

/* document */
html,
body {
  height: 100%;
  width: 100%;
  font-size: 0.75vw;
}
body {
  color: black !important;
  font-family: "Baloo Thambi 2";
}

/* html file style */
#root {
  align-items: left;
  height: 100vh;
}

.App {
  padding: 0 0;
  margin: 0 0;
  height: 100vh;
}

.panel {
  width: 385px !important;
  max-width: 75% !important;
  border-right: 10px rgb(98, 102, 74);
  box-shadow: inset 1px 0px 2.5vh 2.5vh rgba(132, 138, 101, 0.925);
  background-color: #aab185;
}

.panelSection {
  margin: 2.85vh 0;
}

.canvas {
  box-shadow: inset 1px 0px 20px 20px rgba(132, 138, 101, 0.514);
  background-size: cover;
  background: radial-gradient(rgba(121, 126, 92, 0.1) 15%, transparent 16%) 0 0,
    radial-gradient(rgba(121, 126, 92, 0.1) 15%, transparent 16%) 8px 8px,
    radial-gradient(rgba(255, 255, 255, 0.1) 15%, transparent 20%) 0 1px,
    radial-gradient(rgba(255, 255, 255, 0.1) 15%, transparent 20%) 8px 9px;
  background-color: #959b74;
  background-size: 16px 16px;
  display: inline-block;
}

.displayArea {
  margin: 0 0;
  padding: 0 0;
  width: 100%;
  height: 100%;
}

